import React from "react";
import "@elastic/eui/dist/eui_theme_light.css";
import ElasticSearchAPIConnector from "@elastic/search-ui-elasticsearch-connector";
import './App.css';

import {
  ErrorBoundary,
  SearchProvider,
  SearchBox,
  Results,
  PagingInfo,
  ResultsPerPage,
  Paging,
  Sorting,
  WithSearch
} from "@elastic/react-search-ui";
import {
  Layout,
} from "@elastic/react-search-ui-views";
import "@elastic/react-search-ui-views/lib/styles/styles.css";

const connector = new ElasticSearchAPIConnector({
  host: "https://gimli-eu-west-1.searchly.com",
  index: "cad",
  connectionOptions:{
    headers: {
    'Authorization': 'Basic ' + btoa('readonly:jsap79x7yxia9umigfhlsyn51clefqwj')
    }
  }
});

const config = {
  debug: true,
  alwaysSearchOnInitialLoad: true,
  apiConnector: connector,
  hasA11yNotifications: true,
  searchQuery: {
    filters: [],
    search_fields: {
      content: {}
    },
    result_fields: {
      filename: { raw: {} },
      path: { raw: {} },
      size: { raw: {} },
      edited: { raw: {} },
      content: {
        snippet: {
          size: 100,
          fallback: false
        }
      }
    }
  }
};

const SORT_OPTIONS = [
  {
    name: "Relevance",
    value: []
  },
  {
    name: "File name (Asc)",
    value: [
      {
        field: "filename",
        direction: "asc"
      }
    ]
  },
  {
    name: "Size (Desc)",
    value: [
      {
        field: "size",
        direction: "desc"
      }
    ]
  },
  {
    name: "Edited (Desc)",
    value: [
      {
        field: "edited",
        direction: "desc"
      }
    ]
  }
];

export default function App() {
  return (
    <SearchProvider config={config}>
      <WithSearch
        mapContextToProps={({ wasSearched }) => ({
          wasSearched
        })}
      >
        {({ wasSearched }) => {
          return (
            <div className="App">
              <ErrorBoundary>
                <Layout
                  header={
                    <SearchBox
                      autocompleteSuggestions={false}
                    />
                  }
                  sideContent={
                    <div>
                      {wasSearched && (
                        <Sorting label={"Sort by"} sortOptions={SORT_OPTIONS} />
                      )}
                    </div>
                  }
                  bodyContent={
                    <Results
                      titleField="filename"
                      urlField="path"
                    />
                  }
                  bodyHeader={
                    <React.Fragment>
                      {wasSearched && <PagingInfo />}
                      {wasSearched && <ResultsPerPage />}
                    </React.Fragment>
                  }
                  bodyFooter={<Paging />}
                />
              </ErrorBoundary>
            </div>
          );
        }}
      </WithSearch>
    </SearchProvider>
  );
}
